@font-face {
  font-family: "TT Norms";
  src: local("./fonts/TT Norms Regular"),
    local("./fonts/TT Norms Regular"),
    url("./fonts/TTNorms-Regular.woff2") format("woff2"),
    url("./fonts/TTNorms-Regular.woff") format("woff"),
    url("./fonts/TTNorms-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
  
@font-face {
  font-family: "TT Norms";
  src: local("./fonts/TT Norms Medium"),
    local("./fonts/TT-Norms-Medium"),
    url("./fonts/TTNorms-Medium.woff2") format("woff2"),
    url("./fonts/TTNorms-Medium.woff") format("woff"),
    url("./fonts/TTNorms-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "TT Norms";
  src: local("./fonts/TT Norms Bold"),
    local("./fonts/TT-Norms-Bold"),
    url("./fonts/TTNorms-Bold.woff2") format("woff2"),
    url("./fonts/TTNorms-Bold.woff") format("woff"),
    url("./fonts/TTNorms-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
